import { createNamespacedHelpers } from 'vuex';

const { mapState, mapActions } = createNamespacedHelpers('storeOne');
export default {
	data() {
		return {
			titleNViewBackground: '',
			swiperCurrent: 0,
			swiperLength: 0,
			carouselList: [],
			goodsList: [],
			huadongs: [
				{ img: '/static/temp/c3.png', mag: '123546854' },
				{ img: '/static/temp/c5.png', mag: '123546854' },
				{ img: '/static/temp/c6.png', mag: '123546854' },
				{ img: '/static/temp/c7.png', mag: '123546854' },
				{ img: '/static/temp/c3.png', mag: '123546854' },
				{ img: '/static/temp/c5.png', mag: '123546854' },
				{ img: '/static/temp/c6.png', mag: '123546854' },
				{ img: '/static/temp/c7.png', mag: '123546854' }
			],
			huadongs1: [{ img: '/static/temp/c3.png', mag: '123546854' }, { img: '/static/temp/c5.png', mag: '123546854' }, { img: '/static/temp/c6.png', mag: '123546854' }],
			...mapState(['num'])
		};
	},
	onLoad() {
		this.loadData();
		// 请求为您推荐数据
		this.$request
			.GET({
				url: '/v1/goods/recommend',
				data: {
					page: 1
				}
			})
			.then(res => {
				this.goodsList = res;
			});
	},
	methods: {
		...mapActions(['ADD', 'GOODS_RECOMMEND']),
		/**
		 * 请求静态数据只是为了代码不那么乱
		 * 分次请求未作整合
		 */
		scroll(e) {
			this.old.scrollTop = e.detail.scrollTop;
		},
		async loadData() {
			const carouselList = await this.$api.json('carouselList');
			this.titleNViewBackground = carouselList[0].background;
			this.swiperLength = carouselList.length;
			this.carouselList = carouselList;
		},
		// 详情页
		navToDetailPage(item) {
			// 测试数据没有写id，用title代替
			const id = item.title;
			uni.navigateTo({
				url: `/pages/product/product?id=${id}`
			});
		}
	},




























};
